.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* MY CSS Starts */

.img-more-collection{
  max-width: 100%;
  height: 250px;
  width: 100%;
}

.contract-hover{
  cursor: pointer;
}

.contract-hover:hover{
  color: #01a9fa !important;
}

.loader-content-center{
  /* height: 100vh; */
  position: absolute;
  left: 50%;
  top: 20%;
}

.share-btns-list {
  display: inline-block;
  /* float: right; */
  padding: 0;
}

.social--btn .dropdown-menu .fa, .social--btn .dropdown-menu .fab{
  color: #ffffff;
  background: #01a9fa;
  margin: -5px 0 0 -35px;
  display: block;
  position: absolute;
  padding: 5px;
  border-radius: 100%;
  -moz-border-radius: 100%;
  -webkit-border-radius: 100%;
}
.social--btn .dropdown-menu a{
  margin: 0 0 10px 30px;
  padding: 20px
}

.collection-social{
  padding: 10px;
}

.customSwitch .custom-control-label{
  color: #fff;
}

.colorSwitch .custom-control-label{
  color: #01a9fa;
}

.fa-heart{
  cursor: pointer;
}

.profileInput{
  height: 38px !important;
}


.popular-img {
  max-width: 100%;
  height: 200px;
  width: 100%;
}

.bidOfferSpan {
  display: inline-block;
  text-align: right;
}

/* MY CSS Ends */