side-bar-filter .collection-list li a .txt-pnl{display:inline-block;float:left;width:calc(100% - 40px);padding-left:15px;text-align:left;padding-top:10px}.side-bar-filter .collection-list li a:hover{color:#01a9fa}.side-bar-filter .collection-list li:last-child{padding-bottom:0;margin-bottom:0}@media(max-width: 1450px){.side-bar-filter{top:0px;border-top:1px solid #ccc}}@media(max-width: 1200px)and (min-width: 992px){.navbar.navbar .logo{margin-left:30px;position:relative;top:-15px}}@media(max-width: 991px){.side-bar-filter{top:unset;height:60px;overflow:hidden}.side-bar-filter.active{height:100vh;overflow-y:scroll;overflow-x:hidden}}.side-bar-filter .collection-list li a .txt-pnl{color:#fff}